import { Injectable } from '@angular/core';
import { combineLatest, BehaviorSubject, Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';

export enum DM_THEME {
  DEFAULT = 'DEFAULT',
}

export enum DM_THEME_MODE {
  LIGHT = 'LIGHT',
  DARK  = 'DARK',
}

export interface IDmThemeParams {
  id: DM_THEME;
  mode: DM_THEME_MODE;
}

const DM_THEME_CLASS_PREFIX = 'dm-ui-theme-';

@Injectable({ providedIn: 'root' })
export class ThemeService {
  theme$: Observable<IDmThemeParams>;

  private currentTheme$: BehaviorSubject<DM_THEME>     = new BehaviorSubject<DM_THEME>(DM_THEME.DEFAULT);
  private currentMode$: BehaviorSubject<DM_THEME_MODE> = new BehaviorSubject<DM_THEME_MODE>(DM_THEME_MODE.LIGHT);

  constructor () {
    this.theme$ = combineLatest([
      this.currentTheme$,
      this.currentMode$,
    ]).pipe(
      map(([id, mode]) => ({ id, mode })),
      shareReplay({
        bufferSize: 1,
        refCount: true,
      }),
    );

    this.theme$.subscribe(newClassName => {
      this.applyTheme(newClassName);
    });
  }

  setTheme (theme: DM_THEME): void {
    this.currentTheme$.next(theme);
  }

  setMode (mode: DM_THEME_MODE): void {
    this.currentMode$.next(mode);
  }

  private applyTheme ({ id, mode }: IDmThemeParams) {
    const newClassName = `${DM_THEME_CLASS_PREFIX}${id}--${mode}`.toLowerCase();

    document.body.classList.forEach(className => {
      if (className.startsWith(DM_THEME_CLASS_PREFIX)) {
        document.body.classList.remove(className);
      }
    });
    document.body.classList.add(newClassName);
  }
}
