import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { IMessage } from '@dimaha/api-interfaces';
import { Subject } from 'rxjs';
import io from 'socket.io-client';

@Component({
  selector: 'dm-palette',
  templateUrl: './palette.component.html',
  styleUrls: ['./palette.component.scss'],
})
export class PaletteComponent implements OnInit {
  hello$ = this.httpClient.get<IMessage>('/api/hello');
  wsEvents$ = new Subject();

  fillerContent = Array.from({ length: 10 }, () =>
    `Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut
       labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco
       laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in
       voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat
       cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.`);

  constructor (private httpClient: HttpClient) {
  }

  ngOnInit (): void {
    // TODO wrap with rxjs, put to service
    // TODO make config
    const socket = io('http://localhost:3333');
    socket.emit('events', { name: 'Nest' }, (responseMessage) => {
      console.warn('ws client response:', responseMessage);
    });

    socket.on('events', (message) => {
      console.warn('ws client:', message);

      this.wsEvents$.next(message);
    });
  }
}
